Html{
	width:100%;
	min-height:780px;
	background-color: #E5E5E5;
	margin: 0, auto;
	font-size: 1.5vw;
}
h1 {
	text-align: center;
}
textarea, input, button {
	outline: none;
}

/* all inputs */
input:focus {
	-webkit-box-shadow: 0px 1px 3px #121212;
	        box-shadow: 0px 1px 3px #121212;
	border-radius: 5px;
}
input::-webkit-input-placeholder {
	color: #7E7E7E ;
	padding-left: 5%;
}
input:-ms-input-placeholder {
	color: #7E7E7E ;
	padding-left: 5%;
}
input::-ms-input-placeholder {
	color: #7E7E7E ;
	padding-left: 5%;
}
input::placeholder {
	color: #7E7E7E ;
	padding-left: 5%;
}
input {
	width: 40%;
  -webkit-box-shadow: 0px 3px 3px #121212;
          box-shadow: 0px 3px 3px #121212;
	border-radius: 10px;
	border: none;
	font-size: 20px;
	margin: 1%;
}
button {
	font-size: 1vw;
	color:#121212;
	background-color: #c8c8c8;
	padding: 1%;
	border-radius: 50px;
}






